.pt_features {
    width: 100%;
    display: flex;
    justify-content: space-between;
    flex-direction: row;
    margin: auto;
    width: 90%;
}

.pt_features-heading {
    flex: 1;
    display: flex;
    justify-content: flex-start;
    align-items: flex-start;
    flex-direction: column;

    text-align: left;
    margin-right: 5rem;
}

.pt_features-heading h1 {
    font-size: 34px;
    line-height: 45px;
    font-weight: 800;
    font-family: var(--font-family);
    color:  var(--font-family);
}

.pt_features-heading p {
    color: #099DFF;
    font-size: 16px;
    line-height: 30px;
    font-weight: 500;
    font-family: var(--font-family);

    margin-top: 2rem;
}

.pt_features-container {
    flex: 1.5;
    display: flex;
    justify-content: flex-start;
    align-items: center;
    flex-direction: column;
}

.pt_features-container h1 {
    color: #042C54;
}

@media screen and (max-width: 990px) {
    .pt_features {
        flex-direction: column;
    }

    .pt_features-heading {
        margin: 0 0 2rem 0;
    }
}

@media screen and (max-width: 550px) {
    .pt_features-heading h1 {
        font-size: 28px;
        line-height: 38px;
    }
}